import React, { useEffect, useState, useCallback } from 'react'
import { useEmblaCarousel } from 'embla-carousel/react'
import { EmblaCarouselType } from 'embla-carousel'
import styled from 'styled-components'
import { color } from '../../Settings'

const CIRCLE_DEGREES = 360
const WHEEL_ITEM_SIZE = 30
const WHEEL_ITEM_COUNT = 18
const WHEEL_ITEMS_IN_VIEW = 5
const WHEEL_ITEM_RADIUS = CIRCLE_DEGREES / WHEEL_ITEM_COUNT
const WHEEL_RADIUS = Math.round(
  WHEEL_ITEM_SIZE / 2 / Math.tan(Math.PI / WHEEL_ITEM_COUNT),
)
const IN_VIEW_DEGREES = WHEEL_ITEM_RADIUS * WHEEL_ITEMS_IN_VIEW

const Wrap = styled.div`
  background-color: ${color.rome};
  min-width: 100%;
  height: 100%;
  overflow: hidden;

  > .viewport {
    height: 100%;
    width: 100%;
    position: relative;
    display: flex;
    align-items: center;
    perspective: 1000px;
    overflow: visible !important;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -webkit-touch-callout: none;
    -khtml-user-select: none;
    -webkit-tap-highlight-color: transparent;
  }

  .viewport--perspective-left {
    perspective-origin: calc(50% + ${100 + WHEEL_ITEM_SIZE}px) 50%;
    transform: translateX(${WHEEL_ITEM_SIZE - 3}px);
  }

  .viewport--perspective-right {
    perspective-origin: calc(50% - ${100 + WHEEL_ITEM_SIZE}px) 50%;
    transform: translateX(${-WHEEL_ITEM_SIZE + 3}px);
  }
`

const Container = styled.div`
  height: ${WHEEL_ITEM_SIZE}px;
  width: 100%;
  position: absolute;
  transform-style: preserve-3d;
  will-change: transform;
`

const Slide = styled.div`
  position: absolute;
  top: 0 !important;
  left: 0 !important;
  width: 100%;
  height: 100%;
  font-size: 18px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  backface-visibility: hidden;
  opacity: 0;
`

const isInView = (wheelLocation: number, slidePosition: number): boolean =>
  Math.abs(wheelLocation - slidePosition) < IN_VIEW_DEGREES

const slideStyle = (
  embla: EmblaCarouselType,
  index: number,
  loop: boolean,
  slideCount: number,
  radius: number,
  totalRadius: number,
) => {
  const wheelLocation = embla.scrollProgress() * totalRadius
  const positionDefault = embla.scrollSnapList()[index] * totalRadius
  const positionLoopEnd = positionDefault - totalRadius
  const positionLoopStart = positionDefault + totalRadius

  let inView = false
  let angle = index * -WHEEL_ITEM_RADIUS

  if (isInView(wheelLocation, positionDefault)) {
    inView = true
  }

  if (loop && isInView(wheelLocation, positionLoopEnd)) {
    inView = true
    angle = -CIRCLE_DEGREES + (slideCount - index) * WHEEL_ITEM_RADIUS
  }

  if (loop && isInView(wheelLocation, positionLoopStart)) {
    inView = true
    angle = -(totalRadius % CIRCLE_DEGREES) - index * WHEEL_ITEM_RADIUS
  }

  if (inView) {
    return {
      opacity: 1,
      transform: `rotateX(${angle}deg) translateZ(${radius}px)`,
    }
  }

  return { opacity: 0, transform: 'none' }
}

type PropType = {
  loop?: boolean
  slideCount: number
  perspective: 'left' | 'right'
}

const Wheel = (props: PropType) => {
  const { slideCount, perspective, loop = false } = props
  const [EmblaCarousel, embla] = useEmblaCarousel({
    loop,
    axis: 'y',
    startIndex: 0,
    draggableClass: '',
    draggingClass: '',
    selectedClass: '',
    dragFree: true,
  })
  const [wheelReady, setWheelReady] = useState(false)
  const [rotateX, setRotateX] = useState(0)
  const totalRadius = slideCount * WHEEL_ITEM_RADIUS
  const rotationOffset = loop ? 0 : WHEEL_ITEM_RADIUS
  const slideIndexes = Array.from(Array(slideCount).keys())
  const containerStyle = {
    transform: `translateZ(${WHEEL_RADIUS}px) rotateX(${rotateX}deg)`,
  }

  const slideStyles = () => {
    const styles = []
    for (let i = 0; i < slideIndexes.length; i += 1) {
      const style = embla
        ? slideStyle(embla, i, loop, slideCount, WHEEL_RADIUS, totalRadius)
        : {}
      styles.push(style)
    }
    return styles
  }

  const setRotation = useCallback(() => {
    if (!embla) return
    const rotation = slideCount * WHEEL_ITEM_RADIUS - rotationOffset
    setRotateX(rotation * embla.scrollProgress())
  }, [setRotateX, slideCount, rotationOffset, embla])

  useEffect(() => {
    if (embla) {
      setWheelReady(true)
      embla.dangerouslyGetEngine().translate.toggleActive(false)
      setRotation()

      embla.on('pointerUp', () => {
        const { scrollTo, target, location } = embla.dangerouslyGetEngine()
        const distanceToTarget = target.get() - location.get()
        scrollTo.distance(distanceToTarget * 0.1, true)
      })

      embla.on('scroll', () => {
        setRotation()
      })

      // embla.on('resize', () => {
      // setWheelReady(false)
      // setWheelReady(() => {
      //   embla.reInit()
      //   embla.dangerouslyGetEngine().translate.toggleActive(false)
      //   setRotation()
      //   return true
      // })
      // })
    }
  }, [embla, setRotation])

  return (
    <Wrap>
      <EmblaCarousel
        className={`viewport viewport--perspective-${perspective}`}
      >
        <Container style={wheelReady ? containerStyle : { transform: 'none' }}>
          {slideStyles().map((slideStyle, index) => (
            <Slide
              key={index}
              style={wheelReady ? slideStyle : { transform: 'none' }}
            >
              {index}
            </Slide>
          ))}
        </Container>
      </EmblaCarousel>
    </Wrap>
  )
}

export default Wheel
