import React, { PropsWithChildren } from 'react'
import { media, transition } from '../../Settings'
import styled, { css } from 'styled-components'
import { H2Style } from '../../Text'
import { Button } from '../../Button'

const SlideThumbnailWrap = styled.div`
  ${H2Style};
  height: 100%;
  position: relative;
  padding-left: 0.3rem;

  ${media.minSm} {
    padding-left: 0.6rem;
  }
  ${media.minMd} {
    padding-left: 0.8rem;
  }
`

const SlideThumbnailButton = styled(Button)`
  position: relative;
  display: block;
  height: 100%;
  width: 100%;
  overflow: hidden;
`

const SlideThumbnailImg = styled.img`
  position: absolute;
  top: 0;
  bottom: 0;
  left: -10000%;
  right: -10000%;
  margin: auto;
  min-width: 1000%;
  min-height: 1000%;
  max-width: none;
  transform: scale(0.1);
  transition: opacity ${transition.default};
`

type PropType = React.HTMLAttributes<HTMLDivElement> & {
  width: number
  imgSrc: string
  selected: boolean
  onClick: () => void
}

export const SlideThumbnail = (props: PropsWithChildren<PropType>) => {
  const { width, imgSrc, selected, onClick, ...restProps } = props

  return (
    <SlideThumbnailWrap
      {...restProps}
      css={css`
        min-width: ${width}%;
      `}
    >
      <SlideThumbnailButton onClick={onClick} tabAccessType="inner">
        <SlideThumbnailImg
          src={imgSrc}
          css={css`
            opacity: ${selected ? 1 : 0.2};
          `}
        />
      </SlideThumbnailButton>
    </SlideThumbnailWrap>
  )
}
