import React, { useState, useEffect, useCallback } from 'react'
import { EmblaOptionsType } from 'embla-carousel'
import { useEmblaCarousel } from 'embla-carousel/react'
import { PrevButton, NextButton } from './NavButton'
import { useInView } from 'react-intersection-observer'
import { useEmblaUtils } from '../hook'
import { SlideLazyload } from './SlideLazyload'
import { media } from '../Media'
import {
  SceneDefault,
  SceneDefaultContainer,
  SceneDefaultPlaceholder,
} from './SceneDefault'

type PropType = {
  slides: number[]
  options?: EmblaOptionsType
}

const Carousel = (props: PropType) => {
  const { slides, options = {} } = props
  const [slidesInView, setSlidesInView] = useState([0])
  const [EmblaCarousel, embla] = useEmblaCarousel({
    ...options,
    selectedClass: '',
    draggableClass: '',
    draggingClass: '',
  })
  const {
    scrollPrev,
    scrollNext,
    prevBtnEnabled,
    nextBtnEnabled,
  } = useEmblaUtils(embla)

  const findSlidesInView = useCallback(() => {
    if (!embla) return

    setSlidesInView((slidesInView) => {
      if (slidesInView.length === embla.slideNodes().length) {
        embla.off('select', findSlidesInView)
      }
      const inView = embla
        .slidesInView(true)
        .filter((index) => slidesInView.indexOf(index) === -1)
      return slidesInView.concat(inView)
    })
  }, [embla, setSlidesInView])

  useEffect(() => {
    if (!embla) return

    embla.on('select', findSlidesInView)
    findSlidesInView()
  }, [embla, findSlidesInView])

  return (
    <SceneDefault>
      <EmblaCarousel>
        <SceneDefaultContainer axis={options.axis}>
          {slides.map((width, index) => (
            <SlideLazyload
              key={index}
              width={width}
              imgSrc={media[index]}
              inView={slidesInView.indexOf(index) > -1}
            />
          ))}
        </SceneDefaultContainer>
      </EmblaCarousel>
      <PrevButton onClick={scrollPrev} enabled={prevBtnEnabled} />
      <NextButton onClick={scrollNext} enabled={nextBtnEnabled} />
    </SceneDefault>
  )
}

const CarouselWithPlaceholder = (props: PropType) => {
  const [ref, inView] = useInView({ triggerOnce: true })

  return (
    <div ref={ref}>
      {inView ? <Carousel {...props} /> : <SceneDefaultPlaceholder />}
    </div>
  )
}

export default CarouselWithPlaceholder
