import React from 'react'
import { useInView } from 'react-intersection-observer'
import { SceneDefaultPlaceholder } from './SceneDefault'
import styled from 'styled-components'
import { color, zindex } from '../../Settings'
import { rgba } from 'polished'
import Wheel from './Wheel'

const WHEEL_ITEM_SIZE = 30
const WHEEL_VIEWPORT_SIZE = 230

const SceneWheel = styled.div`
  background-color: ${color.rome};
  padding-left: 2rem;
  padding-right: 2rem;
`

const SceneInner = styled.div`
  position: relative;
  display: flex;
  width: 100%;
  max-width: 300px;
  margin-left: auto;
  margin-right: auto;

  &:before,
  &:after {
    display: block;
    content: '';
    height: calc(${WHEEL_VIEWPORT_SIZE}px / 2 - ${WHEEL_ITEM_SIZE}px / 2);
    z-index: ${zindex.step};
    pointer-events: none;
    position: absolute;
    left: 0;
    right: 0;
  }
  &:before {
    top: -0.5px;
    background: linear-gradient(
      to top,
      ${rgba(color.rome, 0.6)} 0%,
      ${rgba(color.rome, 0.99)} 100%
    );
    border-bottom: 0.5px solid ${rgba(color.london, 0.3)};
  }
  &:after {
    bottom: -0.5px;
    background: linear-gradient(
      to bottom,
      ${rgba(color.rome, 0.6)} 0%,
      ${rgba(color.rome, 0.99)} 100%
    );
    border-top: 0.5px solid ${rgba(color.london, 0.3)};
  }
`

const WheelWithLabel = styled.div`
  height: ${WHEEL_VIEWPORT_SIZE}px;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 50%;
  line-height: 1;
`

const Label = styled.div`
  font-weight: bold;
  font-size: 18px;
  transform: translateX(${-55}px);
  pointer-events: none;
`

type PropType = {
  loop?: boolean
}

const Carousel = (props: PropType) => {
  const { loop } = props

  return (
    <SceneWheel>
      <SceneInner>
        <WheelWithLabel>
          <Wheel slideCount={24} perspective="left" loop={loop} />
          <Label>hours</Label>
        </WheelWithLabel>
        <WheelWithLabel>
          <Wheel slideCount={60} perspective="right" loop={loop} />
          <Label>min</Label>
        </WheelWithLabel>
      </SceneInner>
    </SceneWheel>
  )
}

const CarouselWithPlaceholder = (props: PropType) => {
  const [ref, inView] = useInView({ triggerOnce: true })

  return (
    <div ref={ref}>
      {inView ? <Carousel {...props} /> : <SceneDefaultPlaceholder />}
    </div>
  )
}

export default CarouselWithPlaceholder
