import React, { useState, useEffect, useCallback } from 'react'
import { EmblaOptionsType } from 'embla-carousel'
import { useEmblaCarousel } from 'embla-carousel/react'
import { PrevButton, NextButton } from './NavButton'
import { useInView } from 'react-intersection-observer'
import { useEmblaUtils } from '../hook'
import { SlideParallax } from './SlideParallax'
import { media } from '../Media'
import {
  SceneDefault,
  SceneDefaultContainer,
  SceneDefaultPlaceholder,
} from './SceneDefault'

const PARALLAX_FACTOR = 1.2

type PropType = {
  slides: number[]
  options?: EmblaOptionsType
}

const Carousel = (props: PropType) => {
  const { slides, options = {} } = props
  const [slideStyles, setSlideStyles] = useState<number[]>([])
  const [EmblaCarousel, embla] = useEmblaCarousel({
    ...options,
    selectedClass: '',
    draggableClass: '',
    draggingClass: '',
  })
  const {
    scrollPrev,
    scrollNext,
    prevBtnEnabled,
    nextBtnEnabled,
    scrollSnaps,
  } = useEmblaUtils(embla)

  const updateSlideStyles = useCallback(() => {
    if (!embla || scrollSnaps.length === 0) return

    const styles = scrollSnaps.map((scrollSnap) => {
      const diffToTarget = scrollSnap - embla.scrollProgress()
      return diffToTarget * (-1 / PARALLAX_FACTOR) * 100
    })
    setSlideStyles(styles)
  }, [embla, setSlideStyles, scrollSnaps])

  useEffect(() => {
    if (!embla) return

    embla.on('scroll', updateSlideStyles)
    updateSlideStyles()
  }, [embla, updateSlideStyles])

  return (
    <SceneDefault>
      <EmblaCarousel>
        <SceneDefaultContainer axis={options.axis}>
          {slides.map((width, index) => (
            <SlideParallax
              key={index}
              width={width}
              imgSrc={media[index]}
              parallax={slideStyles[index]}
            />
          ))}
        </SceneDefaultContainer>
      </EmblaCarousel>
      <PrevButton onClick={scrollPrev} enabled={prevBtnEnabled} />
      <NextButton onClick={scrollNext} enabled={nextBtnEnabled} />
    </SceneDefault>
  )
}

const CarouselWithPlaceholder = (props: PropType) => {
  const [ref, inView] = useInView({ triggerOnce: true })

  return (
    <div ref={ref}>
      {inView ? <Carousel {...props} /> : <SceneDefaultPlaceholder />}
    </div>
  )
}

export default CarouselWithPlaceholder
